/* cookie message */

.cookie-message {
	width: 100%;
	text-align: left;
	position: fixed;
	bottom: -100%;
	left: 0;
	background: #fff;
	display: flex;
	justify-content: center;
	align-items: center;
	padding: 6px 40px;
	transition: 0.4s;
	opacity: 0;
	z-index: 100;
  @media screen and (max-width: 1100px) {
    justify-content: space-between;
    padding: 5px 20px;
  }
  &.view {
    border-top: 1px solid #000;
    bottom: 0;
    opacity: 1;
  }
  p {
    color: #000;
    font-size: 16px;
    margin: 0;
    text-transform: capitalize;
    @media screen and (max-width: 1100px) {
      font-size: 14px;
    }
    @media screen and (max-width: 450px) {
      font-size: 12px;
    }
    a {
      text-decoration: underline;
      color: #000;
    }
  }
  .cookie-close {
    cursor: pointer;
    font-weight: 900;
    text-transform: uppercase;
    padding: 5px 60px;
    background-color: #000;
    border: 1px solid #000;
    color: #fff;
    margin-left: 45px;
    transition: .4s;
    font-weight: 700;
    font-size: 18px;
    line-height: 22px;
    @media screen and (max-width: 1100px) {
      margin-left: 20px;
    }
    @media screen and (max-width: 600px) {
      padding: 5px 40px;
    }
    @media screen and (max-width: 450px) {
      padding: 5px 25px;
      font-size: 14px;
    }
    &:hover {
      background-color: #1d262d;
      color: #fff;
      border-color: #1d262d;
    }
  }
}

/* ------------- form error ------------- */

.feedCaptcha-error {
	color: red;
	font-size: 12px;
	position: absolute;
	bottom: -15px;
	left: 5px;
}


/* ------------- modal ------------- */

.dont-leave-modal {
	display: none;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	justify-content: center;
	align-items: center;
	z-index: 1004;
	overflow-y: auto;
  @media screen and (max-height: 760px) {
    align-items: flex-start;
  }
  &.flex-add {
    display: flex;
  }
  .overlay {
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1005;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 51, 102, 0.9);
    cursor: pointer;
  }
  .window {
    position: relative;
    z-index: 1006;
    width: 540px;
    background: #FCD655;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    animation: modal 0.5s ease-in;
    -webkit-animation: modal 0.5s ease-in;
    -moz-animation: modal 0.5s ease-in;
    text-align: center;
    border-radius: 8px;
    padding: 40px;
    @media screen and (max-height: 760px) {
      margin-top: 5px;
    }
    .icon {
      width: 100px;
      height: 100px;
      border-radius: 50%;
      background: #5653A0;
      display: flex;
      justify-content: center;
      align-items: center;
    }
    h2 {
      font-size: 40px;
      line-height: 48px;
      margin: 15px 0 5px;
    }
    h3 {
      width: 100%;
      font-weight: 400;
      font-size: 30px;
      line-height: 36px;
    }
    hr {
      border: none;
      width: 100%;
      height: 1px;
      background: #000;
    }
  }
}


/* animation open modal */

@keyframes modal {
	0% {
		opacity: 0;
	}

	100% {
		opacity: 1;
	}
}

